@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap);
body,
html {
  margin: 0;
}

body {
  padding: 0;
  font-family: -apple-system, Inter, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  background-color: #0d1116;
  overflow: scroll;
  text-align: center;
  font-family: "Outfit";
}

.container {
  height: 100%;
  background-color: #0d1116;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header-container {
  padding-top: 30px;
}

.header {
  margin: 0;
  font-size: 50px;
  font-weight: bold;
}

.acc-details {
  background: -webkit-linear-gradient(left, #60c657 30%, #35aee2 60%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.sub-text {
  font-size: 25px;
  color: white;
}

.gradient-text {
  background: -webkit-linear-gradient(left, #60c657 30%, #35aee2 60%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cta-button {
  height: 45px;
  border: 0;
  width: auto;
  padding-left: 40px;
  padding-right: 40px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  color: white;
}

.connect-wallet-button {
  background: -webkit-linear-gradient(left, #60c657, #35aee2);
  background-size: 200% 200%;
  -webkit-animation: gradient-animation 4s ease infinite;
          animation: gradient-animation 4s ease infinite;
}
.minting {
  background: -webkit-linear-gradient(left, #60c657, #35aee2);
  background-size: 200% 200%;
  -webkit-animation: gradient-animation 4s ease infinite;
          animation: gradient-animation 4s ease infinite;
  cursor: not-allowed;
}

.mint-button {
  background: -webkit-linear-gradient(left, #a200d6, #ff6fdf);
  background-size: 200% 200%;
  -webkit-animation: gradient-animation 4s ease infinite;
          animation: gradient-animation 4s ease infinite;
  margin-right: 15px;
}

.opensea-button {
  background-color: rgb(32, 129, 226);
}

.mint-count {
  color: white;
  font-size: 18px;
  font-weight: bold;
}

.footer-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 30px;
}

.twitter-logo {
  width: 35px;
  height: 35px;
}

.footer-text {
  color: white;
  font-size: 16px;
  font-weight: bold;
}

/* KeyFrames */
@-webkit-keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.mined-details {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: min(400px, 60%);
  margin: 0 auto;
}
.mined-animation {
  -webkit-animation: down-grow 1s ease-in-out forwards;
          animation: down-grow 1s ease-in-out forwards;
}
@-webkit-keyframes down-grow {
  0%{
    -webkit-transform: translateY(0) scale(0);
            transform: translateY(0) scale(0);
  }
  100% {
    -webkit-transform: translateY(15vh) scale(1);
            transform: translateY(15vh) scale(1);
  }
}
@keyframes down-grow {
  0%{
    -webkit-transform: translateY(0) scale(0);
            transform: translateY(0) scale(0);
  }
  100% {
    -webkit-transform: translateY(15vh) scale(1);
            transform: translateY(15vh) scale(1);
  }
}
.external-links {
  color: white;
  text-decoration: none;
}
.external-links:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

